import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import { styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import ToggleButton from "@mui/material/ToggleButton";
import React, { useState } from "react";
import UploadFile from "./UploadFile";
import TextareaProps from "./TextareaProps";
import TextFieldProps from "./TextFieldProps";

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === "dark" ? "transparent" : "#fff",
  ...theme.typography.body2,
  padding: theme.spacing(4),
}));

const ApplyForm = () => {
  const [name, setName] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [email, setEmail] = useState("");
  const [companyName, setCompanyName] = useState("");
  const [companyLocation, setCompanyLocation] = useState("");
  const [companyPostalCode, setCompanyPostalCode] = useState("");
  const [linkedin, setLinkedin] = useState("");
  const [description, setDescription] = useState("");
  const [aspiration, setAspiration] = useState("");
  const [progress, setProgress] = useState("");

  const [alignment, setAlignment] = useState("entrepreneur");

  const handleChange = (event, newAlignment) => {
    setAlignment(newAlignment);
  };

  return (
    <Container>
      <Item className="item">
        <Grid container spacing={4}>
          <Grid item lg={12} md={12} sm={12} xs={12} py={5} className="main-col1">
            <Grid container spacing={2}>
            
            
            {/* {TITLE & TEXT} */}
              <Grid item lg={12} md={12} sm={12} xs={12} py={5}>
                <Typography className="safe-hands">
                  You are in safe hands!
                </Typography>
                <Typography className="note">
                  <b>
                    Note that the information shared is for our own internal
                    review and will not be disclosed to any third party without
                    your consent. We wish you the best of luck and we will
                    endeavor to get back to you regarding the status of your
                    application as soon as possible
                  </b>
                </Typography>
              </Grid>
              
              {/* {TOGGLE BUTTONS} */}
              <Grid item lg={12} md={12} sm={12} xs={12} py={5}>
                <Typography className="safe-hands">
                  I am here to apply as an
                </Typography>
                <ToggleButtonGroup
                  value={alignment}
                  exclusive
                  onChange={handleChange}>
                  <ToggleButton value="entrepreneur">Entrepreneur</ToggleButton>
                  <ToggleButton value="investor">Investor</ToggleButton>
                </ToggleButtonGroup>
              </Grid>

              {/* {FULL NAME} */}
              <TextFieldProps
                label="Full Name"
                type="text"
                name="name"
                value={name}
                change={(e) => setName(e.target.value)}
              />

              {/* {EMAIL} */}
              <TextFieldProps
                label="Email"
                type="email"
                name="email"
                value={email}
                change={(e) => setEmail(e.target.value)}
              />

              {/* {PHONE NUMBER} */}
              <TextFieldProps
                label="Phone Number"
                type="tel"
                name="phone"
                value={phoneNumber}
                change={(e) => setPhoneNumber(e.target.value)}
              />

              {/* {COMPANY NAME} */}
              <TextFieldProps
                label="Company Name"
                type="text"
                name="company"
                value={companyName}
                change={(e) => setCompanyName(e.target.value)}
              />

              {/* {COMPANY LOCATION} */}
              <TextFieldProps
                label="Company's Location"
                type="text"
                name="location"
                value={companyLocation}
                change={(e) => setCompanyLocation(e.target.value)}
              />

              {/* {COMPANY POSTAL CODE} */}
              <TextFieldProps
                label="Company's Postal Code"
                type="text"
                name="postal"
                value={companyPostalCode}
                change={(e) => setCompanyPostalCode(e.target.value)}
              />

              {/* {LINKEDIN LINK} */}
              <TextFieldProps
                label="LinkedIn Link"
                type="url"
                name="linkedin"
                value={linkedin}
                change={(e) => setLinkedin(e.target.value)}
              />

              {/* {BRIEF DESCRIPTION} */}
              <Grid item lg={12} md={12} sm={12} xs={12} py={1}>
                <TextareaProps
                  label="Short Description"
                  name="description"
                  value={description}
                  change={(e) => setDescription(e.target.value)}
                />
              </Grid>

              {/* {FUTURE ASPIRATIONS} */}
              <Grid item lg={12} md={12} sm={12} xs={12} py={1}>
                <TextareaProps
                  label="Future Aspirations"
                  name="aspirations"
                  value={aspiration}
                  change={(e) => setAspiration(e.target.value)}
                />
              </Grid>

              {/* {PROGRESS} */}
              <Grid item lg={12} md={12} sm={12} xs={12} py={1}>
                <TextareaProps
                  label="Progress"
                  name="progress"
                  value={progress}
                  change={(e) => setProgress(e.target.value)}/>
              </Grid>

              {/* {FEASIBILITY STUDY} */}
              <Grid item lg={12} md={12} sm={12} xs={12} py={1} className="upload-col">
                <UploadFile label="Feasibility Study" name="study" id="study" htmlFor="study"/>
              </Grid>
              
              {/* {BUSINESS PLAN} */}
              <Grid item lg={12} md={12} sm={12} xs={12} py={1} className="upload-col">
                <UploadFile label="Business Plan" name="business-plan" id="business-plan" htmlFor="business-plan"/>
              </Grid>

              {/* {APPLY BUTTON} */}
              <Grid item lg={12} md={12} sm={12} xs={12} py={1}>
                <Button className="btn-send">Apply Here</Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Item>
    </Container>
  );
};
export default ApplyForm;
