import * as React from "react";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import { styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';


const Item = styled(Paper)(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === "dark" ? "transparent" : "transparent",
  ...theme.typography.body2,
  padding: theme.spacing(1),
  boxShadow: "none",
}));

const FrequentlyQuestions = () => {

    const [expanded, setExpanded] = React.useState('panel1');

    const handleChange = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    }
  const questions = [
    {
      id: 1,
      panel: "panel1",
      question: "How is the Process?",
      answer: "We once were in your shoes, we wanted to start but did not know",
    },

    {
      id: 2,
       panel: "panel2",
      question: "How do I add my tracking numbers?",
      answer: "We once were in your shoes, we wanted to start but did not know",
    },

    {
      id: 3,
       panel: "panel3",
      question: "What happens if the price changes after I purchase a deal?",
      answer: "We once were in your shoes, we wanted to start but did not know",
    },

    {
      id: 4,
       panel: "panel4",
      question: "When can I expect payment?",
      answer: "We once were in your shoes, we wanted to start but did not know",
    },
    {
      id: 5,
       panel: "panel5",
      question: "Can a buyer get a 1099 form?",
      answer: "We once were in your shoes, we wanted to start but did not know",
    },
  ];

  return (
    <Container className="faq-container">
      <Grid container spacing={1}>
        <Grid item lg={12} md={12} sm={12} xs={12}>
          <Item>
            <Typography className="main-title">Frequently Asked Questions</Typography>
          </Item>
        </Grid>

        {questions.map((item) => (
          <Grid item lg={12} md={12} sm={12} xs={12} key={item.id}>
            <Item>
              <Accordion
                expanded={expanded === item.panel}
                onChange={handleChange(item.panel)}>
                <AccordionSummary
                   expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1bh-content"
                  id="panel1bh-header">
                  <Typography className="question">{item.question}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <Typography className="answer">{item.answer}</Typography>
                </AccordionDetails>
              </Accordion>
            </Item>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};
export default FrequentlyQuestions;






