import * as React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";



const CardsProps = ({
  arrayDetails,
  mainTitle,
  mainText,
  lg,
  md,
  sm,
  xs,
  classCard,
}) => {
  return (
    <Container className={classCard}>
      <Grid container py={4} spacing={2}>
      
        <Grid item lg={12} md={12} sm={12} xs={12}>
          <Typography className="main-title">{mainTitle}</Typography>
        </Grid>

        {mainText && (
          <Grid item lg={12} md={12} sm={12} xs={12}>
            <Typography className="main-text">{mainText}</Typography>
          </Grid>
        )}

        {arrayDetails?.map((item) => (
          <Grid item lg={lg} md={md} sm={sm} xs={xs} py={3} key={item.id}>
            <Card>
              <div className="img-wrap">
                <CardMedia
                  component="img"
                  image={item.image}
                  title={item.alt}
                />
              </div>
              <CardContent>
                <Typography variant="body2" color="text.secondary">
                  {item.description}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};

export default CardsProps;
