import * as React from "react";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import { styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import { NavLink as Link } from "react-router-dom";

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === "dark" ? "transparent" : "transparent",
  ...theme.typography.body2,
  padding: theme.spacing(1),
  boxShadow: "none",
}));

const PanelProps = (props) => {
  const scrollTop = () => {
    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
  };
  return (
    <Container>
      <Grid container spacing={1}>
        <Grid item lg={12} md={12} sm={12} xs={12}>
          {props.title && props.classTitle && (
            <Item>
              <Typography className={props.classTitle}>
                {props.title}
              </Typography>
            </Item>
          )}

          {props.text && props.classText && (
            <Item>
              <Typography className={props.classText}>{props.text}</Typography>
            </Item>
          )}

          {props.button && props.classButton && (
            <Item>
              <div className="wrapper" onClick={scrollTop}>
              <Link to="/apply">
                <Button className={props.classButton}>{props.button}</Button>
              </Link>
              </div>
            </Item>
          )}
          
          {props.srcImage && (
            <Item>
              <img src={props.srcImage} alt="img" />
            </Item>
          )}
        </Grid>
      </Grid>
    </Container>
  );
};
export default PanelProps;
