import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import Toolbar from "@mui/material/Toolbar";
import Container from "@mui/material/Container";
import logo from "../images/default-monochrome.svg";
import Button from "@mui/material/Button";
import CloseIcon from '@mui/icons-material/Close';
import { NavLink as Link } from "react-router-dom";

const Header = () => {
  const scrollTop = () => {
    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
  };

  const navigation = [
    {
      id: 1,
      navItem: "Home",
      to: "/",
      value: "home",
    },
    {
      id: 2,
      navItem: "Why us",
      to: "/why",
      value: "why",
    },
    {
      id: 3,
      navItem: "Services",
      to: "/services",
      value: "services",
    },
    {
      id: 4,
      navItem: "FAQ's",
      to: "/faq",
      value: "faq",
    },
    {
      id: 5,
      navItem: "Contact us",
      to: "/contact",
      value: "contact",
    },
  ];

  const [mobileOpen, setMobileOpen] = React.useState(false);
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  //DRAWER
  const drawer = (
    <Container>
      <Box onClick={handleDrawerToggle} className="main-box">
        <div className="close-btn">
          <CloseIcon />
        </div>
           
        {/* NAVIGATION */}
        <Box className="item-box">
          {navigation.map((item) => (
            <Button key={item.id} onClick={scrollTop}>
              <Link to={item.to} className="nav-link" value={item.value}>
                {item.navItem}
              </Link>
            </Button>
          ))}
          <div className="apply">
            <Button variant="outlined" onClick={scrollTop}>
              <Link to="/apply">Apply</Link>
            </Button>
          </div>
        </Box>
      </Box>
    </Container>
  );

  return (
    <header>
      <Box className="main-box">
        <AppBar component="nav">
          <Container>
            <Toolbar>
              {/* ICON BUTTON */}
              <div className="menu-btn">
                <IconButton
                  aria-label="open drawer"
                  size="large"
                  edge="end"
                  onClick={handleDrawerToggle}
                >
                  <MenuIcon />
                </IconButton>
              </div>

              {/* LOGO IMAGE */}
              <Button className="btn-home" onClick={scrollTop}>
                <Link to="/">
                  <img src={logo} className="logo" alt="logo" width="160px" />
                </Link>
              </Button>

              {/* NAVIGATION */}
              <Box className="item-box">
                {navigation.map((item) => (
                  <Button key={item.id} onClick={scrollTop}>
                    <Link to={item.to} className="nav-link" value={item.value}>
                      {item.navItem}
                    </Link>
                  </Button>
                ))}
              </Box>
              <Button variant="outlined" onClick={scrollTop}>
                <Link to="/apply">Apply</Link>
              </Button>
            </Toolbar>
          </Container>
        </AppBar>

        <Box component="nav">
          <Drawer
            className="drawer"
            variant="temporary"
            anchor="right"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            ModalProps={{
              keepMounted: true,
            }}
          >
            {drawer}
          </Drawer>
        </Box>
      </Box>
    </header>
  );
};

export default Header;
